@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background: white url(/static/media/watercolor.96b5713a.png);
    background-attachment: fixed;
}
header{
    background-color: rgba(255, 255, 255, 0.699);
}
nav{
    display: inline-flex;
    float: right;
}
nav > a{
    color: black;
    text-decoration: none;
    text-transform: capitalize;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    padding: 1em;
}
nav > a:hover{
    background-color: rgba(0, 0, 0, 0.068);
}
header img{
    height: 72px;
    display: inline-block;
}
@media only screen and (max-width: 300px) {
    header a{
        display: block;
        border-bottom: 2px double black;
    }
}
/*Dancing Script*/

.banner{
    background-color: rgba(0, 255, 255, 0.205);
    font-family: 'Verdana';
    font-size: 1em;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
    text-align: center;
}
.banner h4{
    padding: 1em;
    text-transform: capitalize;
    font-family: 'Dancing Script';
    font-size: 2em;
}
#waBtn{
    text-align: center;
    vertical-align: middle;
    text-decoration: none;
    padding: 1em;
    background-color: white;
    border: 2px solid lightgreen;
    border-radius: 4px;
    box-shadow: 2px 2px 4px grey;
}
.figContain{
    border-top: 14px dashed rgba(0, 0, 0, 0.322);
    margin-top: 2em;
    display: flex;
    flex-direction: row;
}
.figContain figure{
    width: 50%;
    height: 700px;
}
#lfig{
    background: url(/static/media/bg-1.a898350c.jpg);
    background-size: 50%;
    background-attachment: fixed;
    background-position: center 0px;
    background-repeat: no-repeat;
}
#rfig{
    background: url(/static/media/bg-2.6da3ca91.jpg);
    background-size: 50%;
    background-attachment: fixed;
    background-position: center bottom;
}
@media only screen and (max-width: 300px) {
    .figContain{
        flex-direction: column;
    }
    .figContain figure{
        border: 3px solid yellow;
        width: 100vw;
        height: 40vh;
    }
    #lfig{
        background-size: cover;
        background-position: center middle;
        background-attachment: scroll;
        background-attachment: initial;
        height: 70vh;
    }
    #rfig{
        background-size: 100%;
    }
    #waBtn{
        padding: 0.2em;
        padding-top: 1em;
    }
}

section{
    background: white;
    padding: 2em;
    margin: 1em;
    font-family: 'cursive';
    font-size: 1.5em;
}
section > div{
    padding: 1em;
    letter-spacing: 0.1em;
}
section > div::before{
    content: '“';
    font-size: 2em;
}
section > div::after{
    content: '„';
    font-size: 2em;
}
section .auther {
    float: right
}
section .auther::before {
    content: '-- ';
}

@media only screen and (max-width: 300px) {
    section{
        padding: .5em;
        margin: .25em;
    }
    section div{
        letter-spacing: 0;
        font-size: medium;
    }
}
